import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import axios from '@/utils/uniAxios.js'
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			nowModel:'现在是收单模式',
			iconName:'arrow-down',
			tabLook:'查看',
			tabInfoShow:false,
			active:0,
			time: 30 * 60 * 60 * 1000,
			songdan: '../../static/delivery/songdan.png',
			jiedan: '../../static/delivery/jiedanset1.png',
			daliveryData: 1,
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '执行订单',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '取单中',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '配送中',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '已送达',
					loadingType: 'more',
					orderList: []
				}
			],
			deliverylist: [{
				state: 1,
				time: '15:12',
				countDown: 30 * 60 * 60 * 1000,
				shopName: '麦田圈官网旗舰店',
				state: 5,
				steps: [{
					text: '步骤一',
					desc: '描述'
				},
				{
					text: '步骤二',
					desc: '描述'
				}],
				info: {
					a: 20,
					b: '麦田圈预单',
					c: '预'
				},
				msg: {
					content:'海苔沙拉炒饭',
					price:'15.00',
					num:1,
					oddNum:'6546545646464546',
					sorce:'校内订单',
					beizhu:'好的',
					biaoshi:25,
					time:'2019-11-14 12:12:00'
				}
			}],
			sendStatus:0,
			page:{
				current:1,
				pageSize:10
			},
			step:{},
			stepa:[],
			stepArrr:[]
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = 0;
		this.loadData('tabChange',3);
	},
	methods: {
		newDate(time){
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			return date
		},
		look(){
			if(!this.tabInfoShow){
				this.tabInfoShow = true;
				this.tabLook = '收起';
				this.iconName='arrow-up';
			}else{
				this.tabInfoShow = false;
				this.tabLook = '查看';
				this.iconName='arrow-down';
			}
			
		},
		// 跳转页面
		sd(e) {
			if(e==1){
				this.songdan = '../../static/delivery/songdan.png';
				this.jiedan = '../../static/delivery/jiedanset1.png';
				this.daliveryData = 1;
				uni.navigateTo({
					url:'deliverylist'
				})
			}else if(e==2){
				this.songdan = '../../static/delivery/songdan2.png';
				this.jiedan = '../../static/delivery/jiedanset.png';
				this.daliveryData = 2
				uni.navigateTo({
					url: 'set'
				})
			}
		},
		//获取订单列表
		loadData(source,n) {
			
			let obj = {
				pageNum:this.page.current,
				pageSize:this.page.pageSize,
				sendStatus:n
			}
			axios.post('/sendInformation/list',obj).then(res=>{
				let result = res.data.data;
				this.deliverylist = res.data.data;
				
				result.forEach(el=>{
					this.stepArrr = [];
					this.stepArrr.push(el.detailAddress,el.receiverDetailAddress)
				})
				this.stepa = []

				for(let i=0;i<this.stepArrr.length;i++){
					this.step = {}
					this.step['text'] = this.stepArrr[i]
					this.step['desc'] = '';
					this.stepa.push(this.step);
					
				}
				
				console.log(this.stepa)
				this.deliverylist.forEach(el=>{
					el['steps'] = this.stepa
				})
			})
			// orderList.forEach(item => {
			// 	navItem.orderList.push(item);
			// })
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			// this.$set(navItem, 'loaded', true);

			//判断是否还有数据， 有改为 more， 没有改为noMore 
			// navItem.loadingType = 'more';

		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			if(this.tabCurrentIndex==0){//已转出
				this.loadData('tabChange',3);
			}else if(this.tabCurrentIndex==1){
				this.nowModel = '现在是取单模式'
				this.loadData('tabChange',0);
			}else if(this.tabCurrentIndex==2){
				this.loadData('tabChange',1);
				this.nowModel = '正在配送'
			}else if(this.tabCurrentIndex==3){
				this.loadData('tabChange',2);
				this.nowModel = '已送达'
			}
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		timestampToTime (time) {
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear()
			var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1)
			var D = date.getDate()
			var h = date.getHours() + ':'
			var m = date.getMinutes() + ':'
			var s = date.getSeconds();
			return Y+'-'+M+'-'+D+' '+h+m+s
		},
		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待支付';
					break;
				case 4:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待收货';
					break;
				case 3:
					stateTip = '待评价';
					break;
				case 5:
					stateTip = '';
					break;
				case 6:
					stateTip = '交易成功';
					break;
				case 7:
					stateTip = '已完成';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		},
	},
}
